.hide {
  display: none;
}

.change-page_list{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 50px 0;
}

.change-page_button-button{
  background: #438bbd;
  border: none;
  border-radius: 3px;
  padding: 15px;
  color: #fff;
  font-size: 30px;
  cursor: pointer;
}

.change-page_button-button.active{
  background-color: #275270;
  box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
}

.change-page_button-button:disabled{
  background-color: #425460;
  cursor: not-allowed;
}

body {
  text-align: center;
  font-family: 'Open Sans', sans-serif;
  color: #3f4850;
}
h2 {
  font-size: 2em;
  margin: 52px 0 40px;
  text-transform: capitalize;
}
ul {
  list-style-type: none;
  padding-left: 0;
}
a {
  text-decoration: none;
}
.size-101vh{
  height: 101vh; /*this is to prevent the small flash while the content is loading on computers that don't always show the scrollbar*/
}
.search-form {
  max-width: 460px;
  display: flex;
  margin: 0 auto 32px;
}
.search-form button {
  outline: none;
  background-color: #438bbd;
  border: none;
  padding: 0px 15px;
  border-radius: 0 0.35em 0.35em 0;
  cursor: pointer;
}
input {
  font-size: 1em;
  width: 100%;
  background-color: #edeff0;
  padding: 10px 15px;
  border: 3px solid #d7dbdf;
  border-right: none;
  border-radius: 0.35em 0 0 0.35em;
  outline: none;
}
.main-nav li {
  width: 100%;
  margin-bottom: 1em;
}
.main-nav a {
  display: block;
  background: #438bbd;
  border-radius: 3px;
  padding: 5px;
  color: #fff;
}
.main-nav .active,
.main-nav a:hover,
.search-form button:hover {
  background-color: #275270;
}
.main-nav .active{
  box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
}
.photo-container img {
  cursor: pointer;
}
.photo-container ul.photo-results {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: flex-start;
}
.photo-container ul.photo-results li {
  width: 220px;
  height: 165px;
  overflow: hidden;
  margin-bottom: 15px;
}
.photo-container ul.photo-results img {
  width: 100%;
  height: 100%;
  transform: scale(1.25);
  transition: transform 1.25s;
}
.photo-container li:hover img {
  transform: scale(1.65);
}
.photo-container .not-found {
  width: 100%;
}

.settings_item{
  padding: 10px;
}

@media only screen and (max-width: 767px) {
  body {
    padding-top: 2em;
  }
  .photo-container li {
    margin: auto;
  }
}

@media only screen and (min-width: 768px) {
  body {
    padding-top: 4em;
  }
  .container {
    max-width: 960px;
    margin: auto;
  }
  .main-nav ul {
    display: flex;
    justify-content: center;
  }
  .main-nav li {
    margin: 10px;
    width: 100px;
  }
  .settings_container{
    display: flex;
    justify-content: center;
  }
  .settings_item{
    margin-left: 10px;
  }
}
